import React, { Component } from "react";
import axios from 'axios'
import './Flow.css'
class Flow extends Component 
{
    constructor()
    {
        super();
        this.state = 
        {
            posts: 
            [
                {
                    id: 1,
                    username: '',
                    il: '',
                    ilce: '',
                    sicaklik: 0,
                    hissedilen_sicaklik: 0,
                    yorum: '',
                    yorum_tarih: ''
                }
            ]
        };
    }

    componentDidMount = () =>
    {
        this.getPosts();
    };

    // Tüm postları çekme
    getPosts = () =>
    {
        axios.get("/getPosts").then(response =>
        {
            let tempArray = []
            for(var i = 0 ; i < response.data.length ; i++)
            {
                tempArray.push
                ({
                    id: response.data[i].id,
                    username: response.data[i].username,
                    il: response.data[i].il,
                    ilce: response.data[i].ilce,
                    sicaklik: response.data[i].sicaklik,
                    hissedilen_sicaklik: response.data[i].hissedilen_sicaklik,
                    yorum: response.data[i].yorum,
                    yorum_tarih: response.data[i].yorum_tarih
                });
            }
            this.setState
            ({
                posts: tempArray
            });
        });
    }

    // Post silme
    deletePost = (post_id) =>
    {
        axios.delete("/deletePost",
        { 
            params: 
            {
                id: post_id
            }
        }, null).then(response =>
        {
        });
        this.getPosts();
    }

    // Favori ekleme
    addFavorite = (post_id) =>
    {
        axios.post("/addFavorite", null,
        { 
            params: 
            {
                id: post_id
            }
        }).then(response =>
        {
        });
        this.getPosts();
    }
    
    render() 
    {
    const post = this.state.posts.map((post) =>
    <li key={post.id}>
        <div className="ui card">
        <div className="content">
            <div className="header">{post.username}</div>
            <div className="header">Yer: {post.il} - {post.ilce}</div>
            <div className="header">Sıcaklık: {post.sicaklik}</div>
            <div className="header">Hissedilen sıcaklık: {post.hissedilen_sicaklik}</div>
            <div className="meta">{post.yorum}</div>
            <div className="description">
            </div>
        </div>
        <div className="extra content">
            {post.yorum_tarih}
            <div className="ui blue labeled submit icon button" onClick={() => this.addFavorite(post.id)}>
            <i className="icon heart"></i>Favorite
            </div>
            <div className="ui blue labeled submit icon button" onClick={() => this.deletePost(post.id)}>
            <i className="icon trash"></i>Delete
            </div>
        </div>
        </div>
    </li>
    );
    return (
        <div className="container">
        {post}
        </div>
    );
    }
}
export default Flow;
