import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Navigation, Footer, Home, Flow, Favorite} from "./components";
import Weather from "./components/Weather";
import createHistory from 'history/createBrowserHistory';

const history = createHistory();
class App extends Component {

  
  render()
  {
    return (
      <div className="App">
        <Router history={history}>
          <Navigation />
          <Switch>
            <Route path="/" exact component={() => <Home />} />
            <Route path="/flow" exact component={() => <Flow />} />
            <Route path="/favorite" exact component={() => <Favorite />} />
            {/* <Route path="/weather/:il" exact component={() => <Weather />} /> */}
            {/* <Route exact path="/weather/:id" render={(props)=>{<Weather id={props.match.params.id}/>}} /> */}
            <Route path="/weather/:il/:ilce/:sicaklik/:hissedilen_sicaklik" component={Weather}/>
          </Switch>
          <Footer />
        </Router>
      </div>
    );
  }
  
}

export default App;