import React, { Component } from 'react'
import axios from 'axios'
import './Weather.css'
import Swal from 'sweetalert2'

class Weather extends Component 
{
    constructor()
    {
        super();
        this.state = 
        {
            comment: '',
            charLimit: 300
        };
        this.handleChangeComment = this.handleChangeComment.bind(this);
    }

    componentDidMount = () =>
    {

    };
    
    // Post ekleme
    addPost = () =>
    {
        axios.post("/post", null, 
        { 
            params: 
            {
                il: this.props.match.params.il,
                ilce: this.props.match.params.ilce,
                sicaklik: this.props.match.params.sicaklik,
                hissedilen_sicaklik: this.props.match.params.hissedilen_sicaklik,
                yorum: this.state.comment
            }
        }).then(response =>
        {
            
        });
        this.toFlow();
    }
    
    // Yorum değiştiğinde düzenleme
    handleChangeComment(event)
    {
        var c = event.target.value + ""
        c = c.substring(0,300)
        this.setState
        ({
            comment: c
        }, () =>
        {
        });
    }

    // Akış sayfasına geçme
    toFlow = () => 
    {
        this.props.history.push("/flow");
    }

    render() 
    {
        return(
            <div className="container">
                <div className="ui_card">
                    <div className="content">
                        <div className="header">İl: {this.props.match.params.il}</div>
                        <div className="header">İlçe: {this.props.match.params.ilce}</div>
                        <div className="header">Sıcaklık: {this.props.match.params.sicaklik}</div>
                        <div className="header">Hissedilen sıcaklık: {this.props.match.params.hissedilen_sicaklik}</div>
                        <div className="meta"></div>
                        <form className="ui reply form">
                            <div className="field">
                                <textarea type="text" value={this.state.comment} onChange={this.handleChangeComment}></textarea>
                            </div>
                            <h2>{this.state.charLimit - this.state.comment.length}/{this.state.charLimit}</h2>
                            <div className="ui blue labeled submit icon button" onClick={this.addPost}>
                                <i className="icon share"></i>Post
                            </div>
                        </form>
                    </div>
                </div>
                
            </div>
        );
    }
}
export default Weather;